<script>
import listSearch from "@/components/list-search/search.vue";
import makeOfficial from './compons/makeOfficial'
const columns = [
  { title: "案号", slot: "2" },
  { title: "申请人" },
  { title: "被申请人" },
  { title: "案件状态" },
  { title: "案件类型" },
  { title: "回避原因", slot: "reason" },
  { title: "文书驳回原因", slot: "txt" },
  { title: "被回避的仲裁员" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch,
    makeOfficial
  },
  data() {
    return {
      searchData: {
        number: "",
        appeal: "",
        regName: [],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "select",
          value: "regName",
          label: "注册名称",
          placeholder: "请选择注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: 10,
      columns: columns,
      tableData: [{ 1: 1, 2: 2 }],
      backDilog: false, // 仲裁员回避
      avoidDilog: false, // 仲裁员回避驳回
      backForm: { backInput: "" },
      backFormRuleValidate: {
        backInput: [
          { required: true, message: "请输入原因", trigger: "change" }
        ]
      },
      avoidForm: {
        textType: "",
        source: "",
        template: "",
        file:"",
        backInput: ""
      },
      avoidFormRuleValidate: {
        textType: [
          { required: true, message: "请选择类型", trigger: "change" }
        ],
        source: [{ required: true, message: "请选择来源", trigger: "change" }],
        template: [
          { required: true, message: "请选择模板", trigger: "change" }
        ],
        backInput: [
          { required: true, message: "请输入驳回原因", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    goSearch() {},
    lookReason(row) {
      this.backDilog = true;
    },
    // 回避驳回
    handleBack(row) {
      this.avoidDilog = true;
      console.log(this.avoidDilog,'avoidDilog')
    },
    // 仲裁员回避提交
    backSubmit() {
      this.$refs["backFormValidate"].validate(valid => {
        console.log(this.backForm);
        if (valid) {
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 回避驳回提交
    avoidSubmit(params) {
    console.log(params,'params')
    },
    // 回避通过
    handleSure(row) {
      this.$Modal.confirm({
        title: "操作",
        content:
          '<div style="text-align:center;">确定通过仲裁员回避请求？</div>',
        onOk: () => {
          this.$Message.success("操作成功");
          this.$Message.error("系统出错，请稍后重试");
        },
        onCancel: () => {}
      });
    },
     handleUpload(file) {
      console.log(file);
      this.avoidForm.file = file;
      return false;
    },
  }
};
</script>
